import store from "../store";

const fetchDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

export const fetchData = () => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    try {
      let totalSupply = await store
        .getState()
        .blockchain.smartContract.methods.totalSupply()
        .call();

      let availableSupply = await store
        .getState()
        .blockchain.smartContract.methods.availableSupply()
        .call();

      let price = await store
        .getState()
        .blockchain.smartContract.methods.PRICE()
        .call();

      let maxPerTx = await store
        .getState()
        .blockchain.smartContract.methods.MAX_PER_TX()
        .call();

      let mintPaused = await store
        .getState()
        .blockchain.smartContract.methods.mintPaused()
        .call();

      let mintEnded = await store
        .getState()
        .blockchain.smartContract.methods.mintEnded()
        .call();

      // let whitelistedOnly = await store
      //   .getState()
      //   .blockchain.smartContract.methods.whitelistedOnly()
      //   .call();
      let whitelistedOnly = true;

      dispatch(
        fetchDataSuccess({
          totalSupply: +totalSupply,
          availableSupply: +availableSupply,
          price: price,
          maxPerTx: +maxPerTx,
          mintPaused,
          mintEnded,
          whitelistedOnly,
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchDataFailed("Could not load data from contract."));
    }
  };
};
