import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchData } from "../redux/data/dataActions";
import { connect } from "../redux/blockchain/blockchainActions";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import Alert from "@mui/material/Alert";
import FilledInput from "@mui/material/FilledInput";
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import RemoveOutlinedIcon from "@mui/icons-material/RemoveOutlined";
import AddOutlinedIcon from "@mui/icons-material/AddOutlined";
import LoadingButton from "@mui/lab/LoadingButton";
import CircularProgress from "@mui/material/CircularProgress";

import CONFIG from "../config.json";
import whitelistedAddresses from "../whitelist.json";

enum MINT_STATE {
  NOT_CONNECTED,
  WRONG_NETWORK,
  CONNECTED,
  NOT_STARTED,
  WHITELISTED_ONLY,
  PUBLIC_SALE,
  PRE_SALE,
  CLAIMING,
  MINTED,
  ERROR,
  CLOSED,
  LOADING,
}

function Actions() {
  const dispatch = useDispatch();
  const data = useSelector<any, any>((state) => state.data);
  const blockchain = useSelector<any, any>((state) => state.blockchain);
  const [claimingNft, setClaimingNft] = useState(false);
  const [mintAmount, setMintAmount] = useState(1);
  const [error, setError] = useState("");
  const [mintReceipt, setMintReceipt] = useState(null);
  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData());
    }
  };

  useEffect(() => getData(), [blockchain.account]);

  function catchError(err: Error) {
    console.error(err);
    if (err.message) {
      setError(err.message);
    } else {
      setError("Something went wrong while trying to mint the NFT");
    }
  }

  const claimNFTs = async () => {
    setClaimingNft(true);
    try {
      const cost = data.whitelistedOnly
        ? CONFIG.WL_WEI_COST
        : CONFIG.PUBLIC_WEI_COST;
      const receipt = await blockchain.smartContract.methods
        .mint(mintAmount)
        .send({
          to: CONFIG.CONTRACT_ADDRESS,
          from: blockchain.account,
          value: cost * mintAmount,
        });
      console.log(receipt);
      setClaimingNft(false);
      setMintReceipt(receipt);
      dispatch(fetchData());
    } catch (err) {
      catchError(err);
      setClaimingNft(false);
    }
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (
      newMintAmount <=
      Math.min(data.maxPerTx, data.availableSupply - data.totalSupply)
    ) {
      setMintAmount(newMintAmount);
    }
  };

  function getMintState(): MINT_STATE {
    if (!blockchain.account || blockchain.smartContract === null) {
      return MINT_STATE.NOT_CONNECTED;
    } else if (blockchain.networkId != CONFIG.NETWORK.ID) {
      return MINT_STATE.WRONG_NETWORK;
    } else if (data.mintPaused) {
      return MINT_STATE.NOT_STARTED;
    } else if (claimingNft) {
      return MINT_STATE.CLAIMING;
    } else if (mintReceipt) {
      return MINT_STATE.MINTED;
    } else if (data.totalSupply >= data.availableSupply || data.mintEnded) {
      return MINT_STATE.CLOSED;
    } else if (data.whitelistedOnly) {
      const isWhitelisted = whitelistedAddresses.includes(blockchain.account);
      return isWhitelisted
        ? MINT_STATE.PUBLIC_SALE
        : MINT_STATE.WHITELISTED_ONLY;
    } else if (data.mintPaused === false) {
      return MINT_STATE.PUBLIC_SALE;
    }
    return MINT_STATE.LOADING;
  }

  if (getMintState() === MINT_STATE.NOT_CONNECTED) {
    return (
      <div>
        <Button
          onClick={(e) => {
            e.preventDefault();
            dispatch(connect());
            getData();
          }}
          variant="contained"
          fullWidth
        >
          Connect Your Wallet
        </Button>
      </div>
    );
  } else if (getMintState() === MINT_STATE.WRONG_NETWORK) {
    return (
      <Alert variant="outlined" severity="error">
        You are not connected to the Ethereum network, please open Metamask and
        make sure you are connected with the right account to the Ethereum
        network.
      </Alert>
    );
  } else if (getMintState() === MINT_STATE.NOT_STARTED) {
    return (
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1.25}
      >
        <Typography variant="h2">Minting is yet to start.</Typography>
        <Typography variant="body1" gutterBottom align="center">
          Once minting starts, you will be able to mint NFTs here.
        </Typography>
      </Stack>
    );
  } else if (
    getMintState() === MINT_STATE.PUBLIC_SALE ||
    getMintState() === MINT_STATE.CLAIMING
  ) {
    return (
      <>
        <Stack direction="row" spacing={4}>
          <FilledInput
            value={mintAmount}
            disableUnderline
            startAdornment={
              <InputAdornment position="start">
                <IconButton
                  aria-label="decrement"
                  size="small"
                  onClick={(e) => {
                    e.preventDefault();
                    decrementMintAmount();
                  }}
                  disabled={getMintState() === MINT_STATE.CLAIMING}
                >
                  <RemoveOutlinedIcon />
                </IconButton>
              </InputAdornment>
            }
            endAdornment={
              <InputAdornment position="start">
                <IconButton
                  aria-label="decrement"
                  size="small"
                  onClick={(e) => {
                    e.preventDefault();
                    incrementMintAmount();
                  }}
                  disabled={getMintState() === MINT_STATE.CLAIMING}
                >
                  <AddOutlinedIcon />
                </IconButton>
              </InputAdornment>
            }
            inputProps={{
              style: {
                textAlign: "center",
              },
            }}
            fullWidth
          />
          <LoadingButton
            disabled={getMintState() === MINT_STATE.CLAIMING}
            loading={getMintState() === MINT_STATE.CLAIMING}
            variant="contained"
            onClick={(e) => {
              e.preventDefault();
              claimNFTs();
              getData();
            }}
            fullWidth
          >
            Mint NFT
          </LoadingButton>
        </Stack>
        {error && (
          <Alert variant="outlined" severity="error">
            {error}
          </Alert>
        )}
      </>
    );
  } else if (getMintState() === MINT_STATE.CLOSED) {
    return (
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1.25}
      >
        <Typography variant="h2">Minting period has ended</Typography>
        <Typography variant="body1" gutterBottom align="center">
          The whole collection be will revealed soon, go to OpenSea to check it
          out!
        </Typography>
        <Button variant="contained" href={CONFIG.MARKETPLACE_LINK}>
          See collection
        </Button>
      </Stack>
    );
  } else if (getMintState() === MINT_STATE.MINTED) {
    return (
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1.25}
      >
        {/* <Typography variant="h2">NFT has been minted</Typography> */}
        <Typography variant="body1" gutterBottom align="center">
          NFT has been minted! It should soon be visible in your OpenSea
          account, you can check the transaction below to follow it's progress.
        </Typography>
        <Button
          variant="contained"
          href={`https://etherscan.io/tx/${mintReceipt.transactionHash}`}
        >
          View Transaction
        </Button>
      </Stack>
    );
  } else if (getMintState() === MINT_STATE.WHITELISTED_ONLY) {
    return (
      <Stack
        direction="column"
        justifyContent="center"
        alignItems="center"
        spacing={1.25}
      >
        <Typography variant="h2">You are not whitelisted</Typography>
        <Typography variant="body1" gutterBottom align="center"></Typography>
      </Stack>
    );
  }
  return (
    <Stack direction="column" justifyContent="center" alignItems="center">
      <CircularProgress size={24} />
    </Stack>
  );
}

export default function Mint() {
  const data = useSelector<any, any>((state) => state.data);
  const cost = data.whitelistedOnly
    ? CONFIG.WL_WEI_COST
    : CONFIG.PUBLIC_WEI_COST;
  return (
    <Stack spacing={2} justifyContent="space-between" flex={1}>
      <Stack direction="row" justifyContent="space-between">
        <Box>
          <Typography variant="subtitle2" align="left" gutterBottom>
            Price
          </Typography>
          <Typography variant="subtitle1" align="left">
            {cost / 1e18} {CONFIG.NETWORK.SYMBOL}
          </Typography>
        </Box>
        <Box>
          <Typography variant="subtitle2" align="right" gutterBottom>
            Total Minted
          </Typography>
          <Typography variant="subtitle1" align="right">
            {data.totalSupply}/{data.availableSupply}
          </Typography>
        </Box>
      </Stack>
      <Actions />
    </Stack>
  );
  return null;
}
